import React, { useState, useEffect } from "react";

const ImageViewer = ({ src, alt = "Image", className = "" }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        if (isOpen) {
            // Delay adding "visible" class for fade-in effect
            setTimeout(() => setIsVisible(true), 10);
        } else {
            // Start fade-out animation
            setIsVisible(false);
        }
    }, [isOpen]);

    useEffect(() => {
        const handleKeyDown = (e) => {
            if (e.key === "Escape") closeLightbox();
        };
        document.addEventListener("keydown", handleKeyDown);
        return () => document.removeEventListener("keydown", handleKeyDown);
    }, []);

    const closeLightbox = () => {
        setIsVisible(false); // Start fade-out animation
        setTimeout(() => setIsOpen(false), 300); // Wait for animation to finish before removing from DOM
    };

    return (
        <>
            {/* Small Image */}
            <img 
                src={"/osgl" + src} 
                alt={alt} 
                className={`image-thumbnail ${className}`}
                onClick={() => setIsOpen(true)}
            />

            {/* Fullscreen Lightbox */}
            {isOpen && (
                <div className={`image-overlay ${isVisible ? "visible" : ""}`} onClick={closeLightbox}>
                    <div className={`image-container ${isVisible ? "visible" : ""}`} onClick={(e) => e.stopPropagation()}>
                        {/* Close Button */}
                        <button className="close-button" onClick={closeLightbox}>✕</button>

                        {/* Enlarged Image */}
                        <img src={"/osgl" + src} alt={alt} className="enlarged-image" />
                    </div>
                </div>
            )}
        </>
    );
};

export default ImageViewer;